import {AlertIcon, ArrowRightIcon} from '@primer/octicons-react';
import {useTranslation} from 'react-i18next';

function Banner(props) {
  const {t} = useTranslation();

  return (
    <div className="Banner fadeInDown" style={{animationDelay: '0.4s'}}>
      <div className="wrapper">
        <div className="alert-icon">
          <AlertIcon size={16} />
        </div>
        <div className="content">
          {t(
            "Hello, I am Pritam Kumar. I have cloned this website from covid19india.org just with the purpose of learning the deployment of this level of project. I don't claim ownership of any content of this website."
          )}{' '}
        </div>
        <a
          href="https://developerpritam.in"
          rel="noreferrer"
          target="_blank"
        >
          {t('Portfolio')}
          <div className="arrow-right-icon">
            <ArrowRightIcon size={16} />
          </div>
        </a>
      </div>
    </div>
  );
  //  return (<> </>);
}

export default Banner;
